.user-create-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
}

.success-message {
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 4px;
    background-color: #e8f5e9;
    color: #388e3c;
    text-align: center;
}

.error-message {
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 4px;
    background-color: #ffebee;
    color: #d32f2f;
    text-align: center;
}
